import React from 'react'
import "./Barn.css"
import redbarn from "./images/redbarn.webp"

function barn() {
    return (
      <div>
        <div className="barn-mission-container">
          <div className="mission-container">
            <h1>Our mission</h1>
            <p>
              Trek started in a small Wisconsin barn in 1976, but our founders
              always saw something bigger. Forty-four years later, we're on a
              mission to make our world a better place to live and ride.
            </p>
            <p>
              
              We build only products we love, provide incredible hospitality to
              our customers, and change the world by getting more people on
              bikes.
            </p>
            <a href="">ALL STORIES</a>
          </div>
          <div className="barn-img-container">
            <img src={redbarn} alt="" />
          </div>
        </div>
      </div>
    );
}

export default barn
