import React from "react";
import "./Mountain.css"
import MountainBike from "./images/MountainBike.webp";

function Mountain() {
  return (
    <div>
      <div className="mountain-image-and-header">
        <img src={MountainBike} alt="" />
        <div className="mountain-header-button-cont">
          <h1>The trails are calling</h1>
          <a href="">SHOP MOUNTAIN BIKES</a>
        </div>
      </div>
    </div>
  );
}

export default Mountain;
