import React from "react";
import "./Generation.css";
import generation from "./images/generation.jpg";
function Generation() {
  return (
    <div>
      <div className="generation-img-and-header-container">
        <div className="generation-header">
          <h1>We're on a mission to change the world through bikes</h1>
        </div>

        <div className="generation-img-and-text-cont">

            <div className="gen-image-cont">  <img src={generation} alt="" /></div>
        

          <div className="generation-text-and-button">
            <h1>Cycling's next generation of leadership</h1>
            <a href="">Read the Story</a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Generation;
