import React from 'react'
import style from "./HelmetShoes.module.css";
import helmet from './images/helmet.webp'
import shoes from './images/shoes.webp'

function HelmetShoes() {
    return (
      <div>
        <div className={style.hscomponentcontainer}>


          <div className={style.columncont}>
          <div className={style.helmetmaincontainer}>
            <div className={style.helmetimgcontainer}>
              <img src={helmet} alt="" />
            </div>
            <div className={style.helmetinfo}>
              <h1>WaveCel: a TIME Magazine Best Invention</h1>
            </div>
            <div className={style.helmetlink}>
             
                <p> <a href="#">Shop WaveCel <i class="fas fa-arrow-right"></i></a> </p>
              
            </div>
          </div>

          <div className={style.shoesmaincontainer}>
            <div className={style.shoesimgcontainer}>
              <img src={shoes} alt="" />
            </div>
            <div className={style.shoesinfo}>
              <h1>A limited take on a classic shoe design</h1>
            </div>
            <div className={style.shoeslink}>
            <p> <a href=""> Shop now <i class="fas fa-arrow-right"></i></a></p>
            </div>
          </div>
</div>




        </div>



      </div>
    );
}

export default HelmetShoes
