import React from "react";
import "./Runup.css";
import runup from "./images/runup.webp";

function Runup() {
  return (
    <div>
      <div className="runup-container">
        <div className="runup-image">
          <img src={runup} alt="" />
        </div>

        <div className="runup-right-side">
          <div className="runup-heading-button-cont">
            <h2>An inside look at women's professional cycling</h2>
            <a href="">WATCH NOW</a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Runup;
