import React from "react";
import "./GreyNavbar.css";
import flag from "./images/us.svg";

function GreyNavbar() {
  return (
    <div>
      <div className="navcontainer">
        {" "}
        <nav className="gnav-cont">
          <div className="gnav-logo-cont">
            <h1 className="gnav-trek"> TREK</h1>
            <h1 className="gnav-electra">Electra</h1>
            <h1 className="gnav-BONTRAGER">BONTRAGER</h1>
            <h1 className="gnav-trek">
              TREK<span className="travel">TRAVEL</span>{" "}
            </h1>
          </div>

          <div className="gnav-user-cart-flag">
            <div className="flag-cont">
              <img src={flag} alt="" />
            </div>
            <i class="fas fa-user"></i>
            <i class="fas fa-shopping-cart"></i>
          </div>
        </nav>
        <nav className="blacknav-cont">
          <div className="bnav-icon">TREK</div>
          <div className="bnav-categ">
            <h3>BIKES</h3>
            <h3>ELECTRIC BIKES</h3>
            <h3>EQUIPMENT</h3>
            <h3>APPAREL</h3>
            <h3>CLEARANCE</h3>
            <h3>BIKE SHOPS</h3>
          </div>

          <div className="bnav-search-cont">
            <div className="bnav-search">
              <div className="search-input-icon-wrapper">
                <i class="fas fa-search"></i>
                <input
                  type="text"
                  placeholder="Search"
                  className="bnav-search"
                />
              </div>
            </div>
          </div>
        </nav>
      </div>
    </div>
  );
}

export default GreyNavbar;
