import React from "react";
import "./HeaderVideo.css";
import ReactPlayer from "react-player/youtube";
import video from "./images/vid.mp4";

function HeaderVideo() {
  return (
    <div>
      <div className="video-heading-cont">
          <div className="vid-heading-text">
              <h1>How to be seen</h1>
              <h3>Tips and gear for safer riding</h3>
              <a href="#">EXPLORE</a>
          </div>
        <video
          style={{
            position: "absolute",
            width: "110%",
            left: "50%",
            top: "50%",
            height: "92%",
            objectFit: "cover",
            transform: "translate(-50%, -50%)",
            textAlign: "center",
            zIndex: "-1",
          }}
          autoPlay
          loop
          muted
        >
          <source src={video} type="video/mp4" />
        </video>
      </div>
    </div>
  );
}

export default HeaderVideo;
