import React from "react";
import duck from "./images/duck.webp";
import plastic from "./images/plastic.webp";
import "./Row1.css";

function Row1() {
  return (
    <div>
      <div className="row1-container">
        <div className="duck-container">
          <img className="row1-img" src={duck} alt="" />
          <div className="row1-header-container">
          <h1>Cycling's best and weirdest trophies</h1>
            <h2>A donkey, a piglet, and the biggest rubber ducky ever</h2>
            <a href="">READ ON</a>
          </div>
        </div>

        <div className="plastic-container">
          <img className="row1-img" src={plastic} alt="" />
          <div className="row1-header-container">
              <h1>It's the little things that count</h1>
            <h2>How we removed 433K lbs. of plastic packaging</h2>
            <a href="">READ THE STORY</a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Row1;
